/*
显示指定用户列表的UI组件
 */
import React, { Component } from "react";
import PropTypes from "prop-types";
import { WingBlank, WhiteSpace, Card } from "antd-mobile";
import { withRouter } from "react-router-dom";
import QueueAnim from "rc-queue-anim";
const Header = Card.Header;
const Body = Card.Body;

class UserList extends Component {
  static propTypes = {
    userList: PropTypes.array.isRequired,
  };
  render() {
    const { userList } = this.props;

    return (
      <WingBlank style={{ marginBottom: 50, marginTop: 50 }}>
        <QueueAnim type="scale">
          {userList.map((user) => (
            <div key={user._id}>
              <WhiteSpace />
              <Card onClick={() => this.props.history.push(`/chat/${user._id}`)}>
                <Header
                  thumb={require(`../../assets/images/head1.png`)}
                  // thumb={require(`../../assets/images/${user.header}.png`)}
                  extra={user.username}
                />
                <Body>
                  <div>
                    <span style={{ display: "inline-block", fontWeight: "bold", width: "100px", textAlign: "right" }}>Job Intention:</span>&nbsp;&nbsp;{user.post}
                  </div>
                  {/* <div>职位: {user.post}</div> */}
                  {user.company ? (
                    <div>
                      <span style={{ display: "inline-block", fontWeight: "bold", width: "100px", textAlign: "right" }}>Company:</span>
                      &nbsp;&nbsp;{user.company}
                    </div>
                  ) : null}
                  {/* {user.company ? <div>公司: {user.company}</div> : null} */}
                  {user.salary ? (
                    <div>
                      <span style={{ display: "inline-block", fontWeight: "bold", width: "100px", textAlign: "right" }}>Salary:</span> &nbsp;&nbsp;{user.salary}
                    </div>
                  ) : null}
                  {/* {user.salary ? <div>月薪: {user.salary}</div> : null} */}
                  <div>
                    <span style={{ display: "inline-block", fontWeight: "bold", width: "100px", textAlign: "right" }}>Description:</span>&nbsp;&nbsp;{user.info}
                  </div>
                  {/* <div>描述: {user.info}</div> */}
                </Body>
              </Card>
            </div>
          ))}
        </QueueAnim>
      </WingBlank>
    );
  }
}

export default withRouter(UserList);



